import code from "../../util/code.js"
import common from "../../util/common.js"
export default {
	data() {
		return {
			content:'',
			status:'',
			key:'',
		};
	},
	onLoad(option) {
		const that = this;
		that.key = option.key;
		//获取招聘信息
		uni.request({
			url: common.port.recruitmentById + '/'+option.key,
			method: "GET",
			dataType: "json",
			data: {},
			success: (result) => {
				console.log(result)
				if (result.data.code == 200) {
					that.content = result.data.data.recruitmentBrochure;
					that.status = result.data.data.applyStatus;
				}else {
					uni.showToast({
						title: result.data.msg,
						icon: 'none',
						duration: 2000,
					})
				}
			}
		})
	},
	methods: {
		//去报名
		gotoStart(data){
			uni.navigateTo({
			    url: '/pages/start/start?key='+data
			});
		},
		back:function(e){
			uni.navigateBack({
			    delta: 1
			});
		}
	}
}
